import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import { connect } from "react-redux";
import { getUUID } from "../actions/fakturAction";
import FormSelisih from "./Components/FormSelisih";
import HeaderCard from "../HeaderCard";

const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
  };
};
const { SearchBar } = Search;
class SelisihTimbangan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kodeJenis",
          text: "Kode",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadarCetak",
          text: "Kadar",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadarTransaksi",
          text: "%",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "nettoData",
          text: "Data Netto(Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "brutoData",
          text: "Data Bruto(Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "brutoSelisih",
          text: "Selisih Bruto(Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "nettoSelisih",
          text: "Selisih Netto(Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "brutoTimbang",
          text: "Timbang Bruto(Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "nettoTimbang",
          text: "Timbang Netto(Rp)",
          sort: true,
          headerStyle: () => {
            return { width: "25%" };
          },
        },
      ],
      data: [
        {
          noFaktur: "-",
          kodeKategori: "-",
          kadarCetak: 0,
          kadarTransaksi: 0,
          kodeJenis: "-",
          noUrut: "-",
          brutoSelisih: 0,
          nettoTimbang: 0,
          nettoData: 0,
          brutoTimbang: 0,
          nettoSelisih: 0,
          brutoData: 0,
        },
      ],
      listLokasi: [],
    };
  }

  componentDidMount() {
    this.props.dispatch(getUUID());
  }
  handleSubmit(data) {
    console.log(data);
    var simpan = {
      noFaktur: this.props.noFaktur,
      noBon: this.props.noFaktur,
      tanggal: data.tanggal,
      kodeLokasi: data.detailData,
      barangdetail: this.state.data,
      footerItem: {
        totalSelisihNetto: 0,
        totalTimbangNetto: 0,
        totalTimbangBruto: 0,
        totalSelisihBruto: 0,
        totalDataBruto: 0,
        totalDataNetto: 0,
      },
    };
    console.log(simpan);
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Selisih Timbangan" />
          <div className="card-body">
            <div className="col-lg-12">
              <FormSelisih
                noFaktur={this.props.noFaktur}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            </div>
            <div className="col-lg-12">
              <ToolkitProvider
                keyField="kodeKelompok"
                data={this.state.data}
                columns={this.state.columns}
                search
              >
                {(props) => (
                  <div className="form-group">
                    <div className="text-right mt-5 mb-3">
                      <SearchBar
                        {...props.searchProps}
                        className="form-control"
                        placeholder="Tuliskan Kode / Nama"
                        style={{ margin: 0, top: "50%" }}
                      />
                    </div>
                    <BootstrapTable
                      {...props.baseProps}
                      striped
                      hover
                      condensed
                      bordered={true}
                      headerClasses="header-table"
                      pagination={paginationFactory()}
                    />
                  </div>
                )}
              </ToolkitProvider>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(SelisihTimbangan);
