import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Axios from "axios";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL;
class FormSelisih extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listLokasi: [],
    };
  }

  componentDidMount() {
    Axios.get(server + "storages2?tipeData=LOKASI")
      .then((res) =>
        this.setState({
          listLokasi: res.data,
        })
      )
      .catch((err) => console.log(err));
  }
  onChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <div className="form-group">
              <Field
                name="tanggal"
                label="Tanggal"
                component={renderField}
                type="date"
              />
            </div>
          </div>
          <div className="col-lg-3">
            <div className="form-group">
              <label htmlFor="">Lokasi</label>
              <Field name="lokasi" component="select" className="form-control">
                <option value="DEFAULT" key="DEFAULT">
                  SILAHKAN PILIH
                </option>
                {this.state.listLokasi.map((list) => (
                  <option value={list.kodeLokasi} key={list.kodeLokasi}>
                    {list.namaLokasi}
                  </option>
                ))}
              </Field>
            </div>
          </div>
          <div className="col-lg-3">
            <div className="form-group">
              <label htmlFor="">Kriteria</label>
              <div className="form-control-transparant">
                <label className="mr-3">
                  <Field
                    id="kodeJenis"
                    name="kriteria"
                    type="radio"
                    value="Kode Jenis"
                    className="mr-2"
                    component="input"
                  />
                  Kode Jenis
                </label>
                <label>
                  <Field
                    id="kadar"
                    name="kriteria"
                    type="radio"
                    value="Kadar"
                    className="mr-2"
                    component="input"
                  />
                  Kadar
                </label>
              </div>
            </div>
          </div>
          <div className="col-lg-3">
            <div className="form-group">
              <label htmlFor="">Detail Data</label>
              <Field
                name="detailData"
                id="detailData"
                className="form-control"
                component="select"
                defaultValue="SEMUA"
              >
                <option value="SEMUA" key="SEMUA">
                  SEMUA
                </option>
                <option value="KOSONG" key="KOSONG">
                  KOSONG
                </option>
              </Field>
            </div>
          </div>
          <div className="col-lg-12 mt-3">
            <div className="text-right">
              <button className="btn btn-primary">Lihat Data</button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormSelisih = reduxForm({
  form: "FormSelisih",
  enableReinitialize: true,
})(FormSelisih);
export default connect()(FormSelisih);
